*{
  margin: 0px;
  padding: 0px;
  font-size: 40px;
}

.form-cont{
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
.inpbox{
  margin: 15px auto;
  display: flex;
  width: 100%;
  flex-direction: column;
}

.game-cont{
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: white;
}

table{
  border-spacing: 0px;
  border: 5px solid red;
}

td{
  border: 1px solid rgb(155, 141, 141);
  color: black;
  background-color: white;
  height: 166px;
  width: 166px;
  font-size: 100px;
}
td:hover{
  cursor: default;
}

.red{
  color:red;
}
.green{
  color: green;
}
.btn{
    display: block;
    background: blue;
    color: white;
    border: none;
    padding: 10px;
    font-size: 30px;
    margin: 10px auto;
    border-radius: 13px;
}
.btn:hover{
  cursor:pointer;
  filter:brightness(80%);
}

input[type="text"]{
  text-align: center;
  display: block;
  width: 100%;
  border:2px solid black;
  font-size: 36px;
  padding: 5px;
  border-radius: 10px;
  box-sizing: border-box;
}

input[type="text"]:focus{
  outline-color: blue;
}

.selected{
  background-color: lightblue;
}

@media screen and (max-width: 1024px){
  *{
    font-size: 30px;
  }
  td{
    height: 140px;
    width: 140px;
    font-size: 80px;
  }
  input[type="text"]{
    font-size: 36px;
  padding: 5px;
  border-radius: 10px;
  }
  .btn{
    padding: 10px;
    font-size: 30px;
    border-radius: 13px;
  }
  
}

@media screen and (max-width: 550px){
  *{
    font-size: 30px;
  }
  td{
    height: 140px;
    width: 140px;
    font-size: 80px;
  }
  input[type="text"]{
    font-size: 25px;
    padding: 5px;
    /* width: 100%; */
    border-radius: 10px;
}
  .btn{
    padding: 10px;
    font-size: 25px;
    border-radius: 13px;
  }
}

@media screen and (max-width: 345px){
  *{
    font-size: 15px;
  }
  td{
    font-size: 35px;
  }
  input[type="text"]{
    font-size: 20px;
    padding: 5px;
    border-radius: 10px;
  }
  .btn{
    padding: 10px;
    font-size: 20px;
    border-radius: 13px;
  }
}
